body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color:#EFF3F7;
  height: 100%;
}

html, #root{
  height:100%;
}
.App {
 padding:50px;
 display:flex;
 justify-content: space-around;
 flex-wrap:wrap;
 font-size:14px
}

.App--loading{
    display:flex;
    justify-content:center;
    align-items: center;
    height:100%;
}

*{margin:0; padding:0}
.Movie{
    background-color:white;
    width:40%;
    display: flex;
    justify-content: space-between;
    align-items:flex-start;
    flex-wrap:wrap;
    margin-bottom:50px;
    text-overflow: ellipsis;
    padding:0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.Movie__Column{
    width:30%;
    box-sizing:border-box;
    text-overflow: ellipsis;
}
.Movie_poster_wrap {text-align:center;}
.Movie__Column:last-child{
    padding:20px 0;
    width:60%;
}

.Movie h1{
    font-size:20px;
    font-weight: 600;
    margin:20px 0 5px
}
.Movie h3 {color:#B4B5BD}
.Movie .Movie__Genres{
    display: flex;
    flex-wrap:wrap;
    margin-bottom:20px;
}

.Movie__Genres .Movie__Genre{
    margin-right:10px;
    color:#B4B5BD;
}

.Movie__torrents  {list-style:none;margin:0; padding:0;display:flex}
.Movie__torrents__title {margin:30px 0 5px}
.Movie__torrents li {list-style:none;margin:0; padding:0;margin-right:20px;}
.Movie__torrents a {text-decoration: none;color:#B4B5BD}

.Movie .Movie__Synopsis {
    text-overflow: ellipsis;
    color:#B4B5BD;
    overflow: hidden;
}

.Movie .Movie__Poster{
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

@media screen and (min-width:1000px) and (max-width:1600px){
    .Movie{
        width:100%;
    }
    .Movie__Column:last-child{
        padding:20px 0;
        width:70%;
    }
}

@media screen and (min-width:668px) and (max-width:999px){
    
    .Movie__Column:last-child{
        padding:20px 0;
        width:60%;
    }
}


@media screen and (min-width:320px) and (max-width:667px){
    .Movie{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Movie{
        width:100%;
        flex-direction: column;
    }
    .Movie__Poster{
        top:0;
        left:0;
        max-width:100%;
    }
    .Movie__Column{
        width:100%!important;
    }
}


